import React, { ReactNode } from "react";
import { FooterWrap, ButtonWrap } from "./styled";

type IDatePickerFooter = {
  extra?: ReactNode;
  timeSwitch?: ReactNode;
  clear?: ReactNode;
  confirm?: ReactNode;
};

export const DatePickerFooter = React.memo(
  (props: IDatePickerFooter) => {
    const { timeSwitch, clear, confirm, extra } = props;

    return (
      <>
        {extra}
        {timeSwitch || clear || confirm ? <FooterWrap>
          {timeSwitch}
          <ButtonWrap>
            {clear}
            {confirm}
          </ButtonWrap>
        </FooterWrap> : null}
      </>
    );
  }
);
